.videoCard 
{
    color: white;
    width: 500px;
    height: 400px;
    padding: 20px;
    transition: transform 100ms;
}
.videoCard:hover 
{
    transform: scale(1.09)
}
.videoCard > img 
{
    height: 300px;
    object-fit: contain;
    width: 500px;
}
.videoCard:hover > .videoCard_stats
{
    display: inline;
}
.videoCard_stats {
    display: none;

}
.videoCard > h2
{
    padding-top:10px;
}